body {
    font-family: KT !important;
}

.outerWrapper {
    background-color: beige;
    background: url(../../static/media/background.a30cc2fe.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.grid-container {
    display: grid;
    grid-template-rows: auto 500px auto;
    height: 100vh;
}

.bottom {
    text-align: center;
    margin: 0px auto;
    padding-top: 40px;
    position: relative;
}

.poolWrapper {
    overflow: hidden;
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
    height: 500px;
    border-style: solid;
    border-color: #ff7807;
    border-radius: 45px;
}

.finalLine {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    background: rgba(236, 128, 89, 0.2);
    height: 135px;
    width: 100%;
    display: grid;
    grid-template-columns: 150px 150px;
}

.result {
    color: #ff7807;
    font-size: 48px;
    box-shadow: 0 6px 5px 0 #ff7807;
    border-radius: 40px;
}

.grid-2-columns {
    display: grid;
    grid-template-columns: 50% 50%;
    width: 600px;
    height: 100%;
    margin: auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.grid-col {
    height: 100%;
    background-repeat: repeat-y;
    background-size: 120px;
    will-change: backgroundPosition;
    transition: 0.3s background-position ease-in-out;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}

.cube {
    background: #ff7f50a1;
    height: 135px;
    width: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    position: absolute;
}

.btnGroup {
    display: inline !important;
    position: relative;
    width: 100%;
}

.styleBtn {
    width: 200px;
    height: 150px;
    border-radius: 30px;
    background: white;
    color: black;
    border-color: #ff7807;
    font-size: 41px;
}

.styleBtn:hover {
    background: #ff7807;
}

.styleBtn:focus {
    background: #ff7807;
}

.styleBtn:active {
    background: #ff7807;
}

.styledRow {
    padding: 100px;
    text-align: center;
}

.centerDiv {
    width: 80%;
    margin: auto;
    text-align: center;
}

.float {
    background: rgba(255, 120, 7, 0.2);
    height: 150px;
    position: absolute;
    top: 180px;
    width: 100%;
    z-index: 100;
}

.floatBolder {
    background: rgba(255, 120, 7, 0.4);
    height: 150px;
    position: absolute;
    top: 180px;
    width: 100%;
    z-index: 101;
}

.top {
    display: -webkit-inline-flex;
    display: inline-flex;
    margin: auto;
}
.modal-body {
  position: relative;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  padding: 1rem;
}

.modal.show .modal-dialog {
  -webkit-transform: none;
  transform: none;
  top: 30%;
  -webkit-transform: translateY(-30%);
          transform: translateY(-30%);
  margin: 10%;
}

.modal-content {
  width: 80vw;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
  margin: auto;
}

.infoWrapper {
  width: 300px;
  height: 300px;
  background: white;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 300px;
  padding: 20px;
}

.hanziWrapper {
  width: 300px;
  height: 300px;
  background: white;
  z-index: 999;
  position: absolute;
  top: 0;
  left: 0px;
}

* {
  box-sizing: border-box;
}

.scene {
  /* border: 1px solid #CCC; */
  position: relative;
  width: 100%;
  height: 140px;
  margin: 0;
}

.carousel {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-transform: translateZ(-288px);
          transform: translateZ(-288px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}

.carousel__cell {
  position: absolute;
  width: 100%;
  height: 120px;
  /* border: 2px solid black; */
  /* line-height: 116px; */
  font-size: 80px;
  font-weight: bold;
  color: black;
  text-align: center;
}

.def-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
}

.square-border-sm {
  width: 200px;
  height: 200px;
}

.square-border-md {
  width: 400px;
  height: 400px;
}

.hanzi-font {
  font-size: 20rem;
}

.pianpang-font {
  font-size: 10rem;
}

.symbol-span {
  font-size: 8rem;
  font-weight: bold;
  color: white;
}

@media only screen and (max-width: 1024px) {
  .square-border-sm {
    width: 100px;
    height: 100px;
  }
  .square-border-md {
    width: 200px;
    height: 200px;
  }
  .hanzi-font {
    font-size: 10rem;
  }
  .pianpang-font {
    font-size: 5rem;
  }
  .symbol-span {
    font-size: 4rem;
    font-weight: bold;
  }
}

.red-border {
  margin-left: 0;
  margin-right: auto;
  background: white;
}

.def-col {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

div {
  text-align: center;
}

.glyphicon-volume {
  background: url(../../static/media/volume.cca2f3c8.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-color: white;
  height: 50px;
  width: 50px;
}

.descriptive-p {
  font-size: 2rem;
  text-align: left;
  color: white;
}

